import Fab from "@material-ui/core/Fab";
import { withStyles } from "@material-ui/core/styles";
import { unstable_useMediaQuery as useMediaQuery } from "@material-ui/core/useMediaQuery";
import React, { useRef, useState } from "react";
import {
  ButtonText,
  ContentWrapper,
  FormHeader,
  FormHeaderParagraph,
  FormInput,
  FormWrapper,
  HeaderH1,
  Logo,
  MainWrapper,
  SecondaryParagraph,
  SecondaryText,
  Sections,
  Wrapper,
  Error
} from "./headerStyles";
import validateEmail from "../../../utils/validateEmail";

type Props = {
  classes: any;
};
const styles = theme => ({
  margin: {
    margin: theme.spacing.unit
  }
});

const Header: React.FunctionComponent<Props> = props => {
  const { classes } = props;
  const matches = useMediaQuery("(min-width:1220.5px)");
  const nameInput = useRef(null);
  const [emailError, setEmailError] = useState(false);
  const [email, setEmail] = useState("");

  const onRefButtonClicked = () => {
    nameInput.current.focus();
  };
  const onSignUpClicked = () => {
    console.log(email);
  };

  const onBlurEmail = () => {
    const valid = validateEmail(email);
    if (!valid) {
      setEmailError(true);
    }
  };

  const onEmailInputChange = e => {
    setEmailError(false);
    setEmail(e.target.value);
    console.log(email);
  };
  return (
    <MainWrapper>
      <Wrapper>
        <Logo />
        <Sections>
          <ContentWrapper>
            <HeaderH1>ECOMMERCE REIMAGINED!</HeaderH1>
            <SecondaryText>
              <SecondaryParagraph>
                Start your online business free with no capital or risk
                investment and make your dreams come true.
              </SecondaryParagraph>
            </SecondaryText>
            {matches ? (
              <>
                <div role="button" onClick={onRefButtonClicked}>
                  <Fab
                    variant="extended"
                    color="primary"
                    aria-label="Add"
                    className={classes.margin}
                  >
                    <ButtonText>Sign Up For Preview</ButtonText>
                  </Fab>
                </div>
              </>
            ) : null}
          </ContentWrapper>
          <div>
            <FormWrapper>
              <FormHeader>
                <FormHeaderParagraph>
                  It's Free For Life!!! Sign Up today and get notified once we
                  launch!
                </FormHeaderParagraph>
              </FormHeader>
              <FormInput ref={nameInput} placeholder="Enter Name" />
              <FormInput
                onChange={e => onEmailInputChange(e)}
                value={email}
                name="email"
                onBlur={onBlurEmail}
                error={emailError}
                placeholder="Enter Email Address"
              />
              {emailError ? <Error>Please Enter a Valid Email</Error> : null}
              <>
                <div role="button" onClick={onSignUpClicked}>
                  <Fab
                    variant="extended"
                    color="primary"
                    aria-label="Add"
                    className={classes.margin}
                  >
                    <ButtonText>Sign Up for preview</ButtonText>
                  </Fab>
                </div>
              </>
            </FormWrapper>
          </div>
        </Sections>
      </Wrapper>
    </MainWrapper>
  );
};
export default withStyles(styles)(Header);
